<template>
  <div class="pa-5 pb-0">
    <Filters
      :filters="[
        {
          filterName: 'version',
          filterType: 'select',
          labelText: 'Версия',
          inputWidth: '260px'
        },
        {
          filterName: 'road',
          filterType: 'select',
          labelText: 'Дороги',
          isRemote: true,
          inputWidth: '260px',
          filterUrl: '/arm-isujt/server/road_filter.json'
        },
        { filterName: 'date', filterType: 'date', labelText: 'За период' }
      ]"
      @reloadTable="updateTable()"
    />
    <Buttons :params="params" />
    <Tabs :tableTypeArray="tabs" :tabModel="tabModel" />
    <DataTable
      :headers="headers"
      :url="url"
      :params="{ date: Math.floor(Date.now() / 1000), load: false }"
      ref="table"
      :tableType="tableType"
    />
  </div>
</template>

<script>
import Filters from '../components/Filters';
import Buttons from '../components/Buttons';
import Tabs from '../components/Tabs';
import DataTable from '../components/DataTable';

export default {
  name: 'PlanningStatistics',
  components: { Buttons, Filters, Tabs, DataTable },
  created() {
    this.$store.state.detailInfo.detailName = 'Детализация';
  },
  computed: {
    getDate() {
      const [year, month, day] = this.$store.state.filtersValues.date.split(
        '-'
      );
      return `${day}.${month}.${year}`;
    }
  },
  methods: {
    getFormattedDate(date) {
      let dateToParams = date;
      if (typeof dateToParams === 'string') {
        let dateFromFilter = new Date(dateToParams);
        dateToParams = Math.floor(dateFromFilter.getTime() / 1000);
      }
      return dateToParams;
    },
    updateTable() {
      this.$refs.table.fetchTableData({
        date: this.getFormattedDate(this.$store.state.filtersValues.date)
      });
    }
  },
  props: ['tabs'],
  data() {
    return {
      panel: 0,
      isFilterOpen: true,
      url: '/arm-isujt/server/statistic.json',
      tableType: 'plan_ind',
      params: {
        buttons: {
          print: true
        },
        name: 'Планирование',
        icon: 'icon icon-calendar'
      },
      tabModel: 3,
      headers: [
        {
          text: '№',
          value: 'row_number',
          isBigText: true,
          border: true
        },
        {
          text: 'Критерий',
          value: 'description',
          border: true,
          alignHeader: 'center'
        },
        {
          text: 'Ед.',
          value: 'units',
          border: true,
          alignHeader: 'center'
        },
        {
          text: 'Значение',
          value: 'value',
          border: true,
          alignHeader: 'center'
        }
      ]
    };
  }
};
</script>
